<script>
import { getNumByUnit, getNumByMeasure } from '@/services';
import { getEditInfo } from '@/api/copy_department/copy_edit_module/copy_edit_detail';

export default {
    name: 'CopyEditPage',
    props: {
        show: false,
        spu: ''
    },
    data () {
        return {

            //语言
            chineseData: {},
            checkLanguage: '',
            languageType: [
                {
                    value: '1',
                    label: '英文'
                },
                {
                    value: '2',
                    label: '德语'
                },
                {
                    value: '3',
                    label: '法语'
                },
                {
                    value: '4',
                    label: '意大利语'
                },
                {
                    value: '5',
                    label: '韩语'
                },
            ],

            leftLanguage: 'CN',

            languageOptions: {
                'CN': {
                    spu_title: ''
                }
            },
            spuImage: [], //买点图

            isGoodsBasic: true,
            isSupplierGoods: true,
            unitData: {
                weight1: ['g', 'kg', 'mg'],
                weight2: ['lb', 'oz'],
                checkWeight1: 'g',
                checkWeight2: 'lb',
                valueWeight1: '',
                valueWeight2: ''
            },
            measureData: {
                measure1: ['cm', 'mm', 'm', 'km'],
                measure2: ['in', 'ft', 'yd'],
                checkMeasure1: 'cm',
                checkMeasure2: 'in',
                valueMeasure1: '',
                valueMeasure2: ''
            },

        };
    },
    watch: {
        spu (val) {
            console.log(val, 'watch');
            if (val) {
                getEditInfo({spu: val}).then(({data}) => {
                    console.log(data, 'data');
                    if (data.status) {
                        this.languageOptions = data.result.copywriters_data;
                        let value = Object.values(this.languageOptions);
                        console.log(value, 'val');
                        value.map((item) => {
                            let child = Object.values(item);

                            child.forEach((childItem) => {
                                console.log(childItem, 11);
                                if (Array.isArray(childItem)) {

                                }
                            });
                        });
                        console.log(value);

                        this.spuImage = data.result.spu_image_data;
                    }
                });
            }
        }
    },
    methods: {
        //翻译相应语言
        handleTranslate (id) {
            this.checkLanguage = id;
        },
        //转换工具
        searchMeasure () {
            let data = this.measureData;
            if (data.valueMeasure1 && !isNaN(data.valueMeasure1)) {
                this.measureData.valueMeasure2 = getNumByMeasure(parseFloat(data.valueMeasure1), data.checkMeasure1, data.checkMeasure2);
            } else {
                this.measureData.valueMeasure2 = '';
            }
        },
        searchWeight1 () {
            let data = this.unitData;
            if (data.valueWeight1 && !isNaN(data.valueWeight1)) {
                this.unitData.valueWeight2 = getNumByUnit(parseFloat(data.valueWeight1), data.checkWeight1, data.checkWeight2);
            } else {
                this.unitData.valueWeight2 = '';
            }
        },

        hideSupplierGoods () {
            this.isSupplierGoods = false;
            this.isGoodsBasic = true;
        },
        //隐藏商品基础资料
        hideGoodsBasic () {
            console.log('a');
            this.isGoodsBasic = false;
            this.isSupplierGoods = true;
        },
        handleClosePage () {
            this.$emit('update:show', false);
        }
    },
    created () {
    }
};
</script>

