    .ui-head {
        background: #f3f3f3;
        height: 45px;
        line-height: 45px;
        padding: 0 20px;
        font-size: 16px;
        position: relative;
        z-index: 2;

        .el-icon-close {
            float: right;
            font-size: 22px;
            cursor: pointer;
            margin-top: 10px;
        }
    }

    //left,right 的公共部分

    .content-box {
        height: 95%;
        flex: 1;
        margin: 20px;
        overflow: auto;
        border: 1px solid @border-color;

        .nav-box {
            background-color: #f3f3f3;
            height: 30px;
            color: @active-color;
            display: flex;
            align-items: center;
            justify-content: space-between;

            p {
                position: relative;
                margin-left: 10px;
                padding-left: 7px;
            }

            p::before {
                content: '';
                position: absolute;
                left: 0px;
                top: 3px;
                width: 2px;
                height: 12px;
                background-color: @active-color;
            }


        }
    }

    .toggle-icon {
        font-size: 16px;
        cursor: pointer;
        color: @active-color;
        position: relative;
        z-index: 9;
    }

    .ui-left {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        padding-right: 420px;
        border-right: 1px solid @border-color;
        border-top: 1px solid @border-color;
        z-index: 1;
        transition: padding-right 0.3s;

        .content-box {

            .translate-box {
                padding: 10px;

                > ul {
                    display: flex;

                    .item1 {
                        width: 140px;
                        height: 40px;
                        text-align: center;
                        line-height: 40px;
                        border: 1px solid @border-color;
                        border-radius: 4px;
                    }

                    .item2 {
                        width: 35px;
                        height: 40px;
                        line-height: 40px;
                        text-align: center;
                    }

                    .item3 {
                        width: 140px;
                    }

                }
            }

            .tabs-box {
                display: flex;
                padding: 0px 10px 10px;

                /deep/ .el-input {
                    width: 100%;
                }

                .tabs-item {
                    flex: 1;

                    .tabs-list {
                        display: flex;
                        padding-left: 10px;
                        position: relative;
                        bottom: -1px;


                        li {
                            cursor: pointer;
                            padding: 0 10px;
                            height: 20px;
                            line-height: 20px;
                            border: 1px solid #f3f3f3;
                            border-bottom: 0;
                            border-top-left-radius: 4px;
                            border-top-right-radius: 4px;
                            color: #000000;
                            text-align: center;
                            background-color: #f3f3f3;
                            margin-right: 10px;

                        }

                        li.active {
                            border: 1px solid @border-color;
                            border-bottom: 0;
                            color: @active-color;
                            background-color: white;
                        }
                    }

                    .form-msg {
                        border: 1px solid @border-color;
                        border-radius: 4px;
                        padding: 10px;

                        .attr-set {
                            display: flex;
                            justify-content: space-between;

                            .space {
                                margin: 0px 5px 0px 10px;
                            }

                            .space:first-child {
                                margin-left: 0;
                            }
                        }
                    }
                }
            }

            .tools-box {
                /deep/ .el-input {
                    width: 100%;
                }

                .unit-tools {
                    display: flex;
                    margin-bottom: 10px;

                    .unit-item {
                        flex: 1;
                        display: flex;
                        align-items: center;
                        margin-right: 50px;

                        .title {
                            width: 100px;
                            line-height: 40px;
                            text-align: right;
                            padding: 0 12px 0 0;
                            font-size: 14px;
                        }

                        > .el-input-group {
                            flex: 1;
                        }

                        .exchange-icon {
                            width: 20px;
                            height: 20px;
                            border-radius: 100%;
                            background-color: #4681e6;
                            color: white;
                            line-height: 20px;
                            text-align: center;
                            margin: 0 20px;
                            transform: rotate(180deg);
                        }
                    }
                }
            }

            .bottom-box {
                text-align: center;
                border-top: 1px solid #e6e6e6;

                button {
                    margin: 25px;
                }

                .orange-btn {
                    border: 1px solid #fe9a33;
                    color: #fe9a33;
                    background: transparent;
                }
            }

        }


    }

    //左边展开
    .ui-left-unfold {
        padding-right: 0;

        display: flex;

        .toggle-box {
            width: 50px;
            font-size: 16px;
            color: @active-color;
            font-weight: bold;
            text-align: center;
            padding-right: 20px;
            background-color: #f3f3f3;
            cursor: pointer;
            position: relative;
            z-index: 9;

        }
    }


    .ui-right {
        right: 0;
        top: 0;
        width: 420px;
        position: absolute;
        height: 100%;
        border-left: 1px solid @border-color;

        transition: width 0.3s;

        .content-box {
            .goods-box {
                padding: 10px;

                ul {
                    display: flex;
                    flex-direction: column;

                    li:last-child {
                        margin-bottom: 0;
                    }

                    li {
                        display: flex;
                        border: 1px solid @border-color;
                        margin-bottom: 10px;

                        img {
                            background-color: #f0f0f0;
                            width: 130px;
                            height: 130px;
                        }

                        .goods-text {
                            flex: 1;

                            p {
                                padding: 5px;
                                padding-bottom: 0;
                                word-break: break-all;
                                overflow: hidden;
                                text-overflow: ellipsis;
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                            }

                            .chinese-text {
                                -webkit-line-clamp: 3;
                            }

                            .foreign-text {
                                -webkit-line-clamp: 4;
                            }

                            .line-solid {
                                border-bottom: 1px solid @border-color;
                                display: block;
                                margin-top: 5px;
                            }
                        }

                    }
                }
            }
        }
    }

    //右边全部展开的样式
    .ui-right-unfold {
        width: 100%;
        display: flex;

        .toggle-box {
            width: 50px;
            font-size: 16px;
            color: @active-color;
            font-weight: bold;
            text-align: center;
            padding-left: 20px;
            background-color: #f3f3f3;
            cursor: pointer;
            position: relative;
            z-index: 9;

        }

        .goods-box {
            ul {
                flex-direction: row !important;

                li {
                    margin-bottom: 10px !important;
                }

                li:nth-child(2n) {
                    margin-left: 10px !important;
                }


            }
        }
    }

    .ui-right-hide {
        width: 0;
    }

    .ui-left, .ui-right {
        top: 0;
        padding-top: 45px;
        box-sizing: border-box;
    }

    /deep/ .el-checkbox {
        margin-right: 20px;
        margin-left: 0;
    }
}

/deep/ .el-collapse {
    border: 0 none;
    overflow: hidden;
}

/deep/ .el-collapse-item {
    .el-collapse-item__header {
        background: #f3f3f3;
        height: 30px;
        line-height: 30px;
        text-align: left;
        padding-left: 10px;
        color: #5a8dfd;

        .el-collapse-item__arrow {
            line-height: 30px;
        }
    }

    .el-collapse-item__wrap {
        border: 0 none;
    }

    .el-collapse-item__content {
        padding: 10px;
        box-sizing: border-box;
    }
}

.bounce-enter-active {
    animation: bounce-in .5s;
}

.bounce-leave-active {
    animation: bounce-in .5s reverse;
}

@keyframes bounce-in {
    0% {
        transform: scale(0) translate(-1000px, -1000px) rotateX(360deg);
    }
    100% {
        transform: scale(1) translate(0, 0) rotateX(0deg);
    }
}


.slide-fade-left-enter-active {
    animation: slide-fade-left-in .3s;
}

.slide-fade-left-leave-active {
    animation: slide-fade-left-in .3s reverse;
}

@keyframes slide-fade-left-in {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(0);
    }
}


</style>



