                span {
                    color: @border-color;
                }

                .icon-tupianshangchuan {
                    font-size: 40px;
                    margin: 0;
                }
            }
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: @upload-height;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}

