    float: none !important;
    clear: both;
}

@upload-height1: 50px;
.ui-com_edit__upload {
    .el-input {
        margin-top: 5px;
        width: 100%;
    }

    .ui-com_edit__upload--title {
        font-size: 14px;
        font-weight: bold;
        text-align: left;
        display: block;
    }

    li {
        float: left;
        width: @upload-height1;
        margin-bottom: 10px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        .ui-com_edit__upload--item > span {
            color: #666;
            font-size: 14px;
            position: absolute;
            bottom: 10px;
            left: 0;
            display: block;
            width: 100%;
        }

        .ui-com_edit__upload--item__img {
            width: 100%;
            height: @upload-height1;

            .img-box {
                width: 100%;
                height: 100%;
            }
        }

        .ui-com_edit__upload--last_btn {
            border: 1px solid #EAEEED;
            width: 100%;
            height: @upload-height1;

            /deep/ .el-upload {
                display: block;
                height: @upload-height1;
                line-height: @upload-height1;
                background: #F5F9F8;

                span {
                    color: @border-color;
                }

                .icon-tupianshangchuan {
                    font-size: 40px;
                    margin: 0;
                }
            }
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height1;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: @upload-height1;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}
/deep/.ui-upload_btn {
    .el-button {
        padding: 0;
    }
}
</style>



