import http from '@/http';
import CONFIG from '@/assets/js/config';

// 获取编辑信息
export function getEditInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    return http.getAjax('/copywriters/copywriters/getCopywritersDetail', data);
}



