<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model="queryData.supplierName" placeholder="请输入供应商代码"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录">
                            <el-select v-model="queryData.supplierType" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.supplierTypeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="样品类型" template="daterange"
                                           v-model="queryData.createTime"></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核结果" template="daterange"
                                           v-model="queryData.createTime"></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作用时">
                            <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.settlementTypeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>

                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.supplierCode" placeholder="支持模糊查询"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierCode" placeholder="支持模糊查询"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-select v-model="queryData.devUser" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.devUserList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核时间">
                            <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.settlementTypeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <transition name="slide-fade" @after-enter="animation()" @after-leave="animation()" @before-enter="animation(1)">
                        <div v-show="filterShow">
                            <li>
                                <SearchRequirement label="质检等级">
                                    <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                        <el-option
                                                v-for="(label, value) in optionList.settlementTypeList"
                                                :key="value"
                                                :label="label"
                                                :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                            <li>
                                <SearchRequirement label="采样价">
                                    <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                        <el-option
                                                v-for="(label, value) in optionList.settlementTypeList"
                                                :key="value"
                                                :label="label"
                                                :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                            <li>
                                <SearchRequirement label="产品属性">
                                    <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                        <el-option
                                                v-for="(label, value) in optionList.settlementTypeList"
                                                :key="value"
                                                :label="label"
                                                :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                            <li>
                                <SearchRequirement label="包装要求">
                                    <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                        <el-option
                                                v-for="(label, value) in optionList.settlementTypeList"
                                                :key="value"
                                                :label="label"
                                                :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                            <li>
                                <SearchRequirement label="说明书">
                                    <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                        <el-option
                                                v-for="(label, value) in optionList.settlementTypeList"
                                                :key="value"
                                                :label="label"
                                                :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button size="small" @click="handleResetQueryInfo">导出当前页</el-button>
                    <el-button size="small" @click="handleQueryInfo({limit: pageData.currentList})" type="primary">导出所有</el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="!filterShow">高级筛选<i class="el-icon-caret-bottom"></i></el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="filterShow">基本筛选<i class="el-icon-caret-top"></i></el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button type="text" size="small" @click="handleClickTask">
                <i class="icon-daochu icon-main"></i>导出当前页
            </el-button>
            <el-button type="text" size="small" @click="handleClickAssign">
                <i class="icon-daochu icon-main"></i>导出所有
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in optionList.moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.moduleType === item.value && 'ui-active'">{{item.label}}</span>
                    </li>
                </el-badge>
            </div>
            <template slot="operation" slot-scope="scope">
                <el-button type="text" @click="handleClickModify(scope.row)">修改</el-button>
            </template>
            <template slot="supplier_code" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.supplier_code}}</p>
                    <div class="ui-table--supplier_code--flag">
                        <span>试卖</span>
                    </div>
                    <div class="ui-table--icon_btn">
                        <i class="icon--yichangfankui" @click="handleClickEdit(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="supplier_name" slot-scope="scope">
                <div>
                    <p>{{scope.row.supplier_name}}</p>
                    <div class="ui-table--icon_btn">
                        <i class="icon--bohui"></i>
                        <i class="icon--wuliuxinxigenzong"></i>
                    </div>
                </div>
            </template>
            <template slot="shipping" slot-scope="scope">
                <div>
                    <p>{{`${scope.row.shipping_province}${scope.row.shipping_city}`}}</p>
                    <p>{{`${scope.row.shipping_area}${scope.row.shipping_address}`}}</p>
                </div>
            </template>
            <template slot="contacts_info" slot-scope="scope">
                <div>
                    <p><i class="icon-quanxianguanli"></i>{{`${scope.row.contacts}/${scope.row.phone}`}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.qq}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.wechat}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.email}}</p>
                </div>
            </template>
            <template slot="payment_info" slot-scope="scope">
                <div>
                    <p><i class="icon-quanxianguanli"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.shop_name}}</p>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <ComDialogRemarks :visible.sync="isShowAssign" title="指派任务" type="select" formLabel="指派任务"
                          :option="optionList.workerList"
                          @confirm="handleConfirmAssign"></ComDialogRemarks>
        <ComDialogRemarks :visible.sync="isShowPriority" title="优先级" type="select" formLabel="优先级"
                          :option="optionList.priorityOption"
                          @confirm="handleConfirmPriority"></ComDialogRemarks>
    </div>
</template>

<script>
import config from '@/assets/js/config'
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage
} from '@/services/index'
import {
    mapActions,
    mapGetters
} from 'vuex'
import {
    getCityInfo
} from '@/api/login'
import {
    getAuditRecordInfo
} from '@/api/quality_control_department/sample_quality_control/audit_record'

export default {
    data () {
        return {

            //高级筛选
            filterShow: false,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                supplierCode: '',
                supplierName: '',
                supplierType: '',
                settlementType: '',
                devUser: '',
                deliverAddress: [],
                paymentMode: '',
                liaisons: '',
                createUser: '',
                createTime: [],
                state: '',
                moduleType: '1'
            },

            optionList: {
                supplierTypeList: {},
                settlementTypeList: {},
                devUserList: {},
                deliverAddressList: [],
                paymentModeList: {},
                liaisonsList: {},
                createUserList: {},
                stateList: {},
                companyTypeList: {},
                moduleTypeList: [
                    {
                        label: '商品台车',
                        value: '1'
                    },
                    {
                        label: '商品SPU',
                        value: '2'
                    }
                ],
                workerList: {
                    '1': '张三',
                    '2': '李四'
                },
                priorityOption: {
                    '1': 'S',
                    '2': 'SS',
                    '3': 'SSS'
                }
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            //指派任务弹窗
            isShowAssign: false,

            //优先级弹窗
            isShowPriority: false,

        }
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'auditRecordInfo'
        ]),
    },
    methods: {
        ...mapActions([
            'GetAuditRecordInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['audit_record'])
            }

            let obj = {
                status: this.queryData.state,
                supplier_code: this.queryData.supplierCode,
                type: this.queryData.supplierType,
                shipping_province: this.queryData.deliverAddress[0],
                shipping_city: this.queryData.deliverAddress[1],
                settle_method: this.queryData.settlementType,
                create_user: this.queryData.createUser,
                devlop_user: this.queryData.devUser,
                supplier_name: this.queryData.supplierName,
                contacts: this.queryData.liaisons,
                pay_method: this.queryData.paymentMode,
                start_time: this.queryData.createTime[0],
                end_time: this.queryData.createTime[1],
                ...params
            }

            this.GetAuditRecordInfo(obj).then(({data}) => {
                // 导出
                if (data.status === 2) return

                this.handleQueryInfoCallBack(data)
            }).catch(err => {
                console.log(err)
            })
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                supplierCode: '',
                supplierName: '',
                supplierType: '',
                settlementType: '',
                devUser: '',
                deliverAddress: [],
                paymentMode: '',
                liaisons: '',
                createUser: '',
                createTime: [],
                state: '',
            }
        },

        //高级筛选
        handleClickFilter() {
            this.filterShow = !this.filterShow;
        },

        animation(isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight()
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true)
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true)
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'audit_record',
                value: JSON.stringify(this.queryData)
            })

            let deliverList = []
            data.select_list.province_info.forEach(value => {
                deliverList.push({
                    ...value,
                    children: []
                })
            })

            // 渲染下拉
            this.optionList.supplierTypeList = data.select_list.sup_type
            this.optionList.settlementTypeList = data.select_list.sup_settle_method
            // this.optionList.devUserList = data.select_list.sup_pay_method;
            this.optionList.paymentModeList = data.select_list.sup_pay_method
            // this.optionList.liaisonsList = data.select_list.sup_pay_method;
            // this.optionList.createUserList = data.select_list.sup_pay_method;
            this.optionList.stateList = data.select_list.sup_status
            this.optionList.companyTypeList = data.select_list.sup_type

            if (!this.optionList.deliverAddressList.length) {
                this.optionList.deliverAddressList = [...deliverList]
            }

            //数据渲染
            this.tableData = data.data.value
            this.tableKey = data.data.key
            this.tableWidth = ['', '', '', '', '220px', '220px']

            // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
            this.tableProps = ['index', 'supplier_code', 'supplier_name', 'shipping', 'contacts_info', 'payment_info', 'operate_dev_user', 'file_url', 'state', 'reject_remarks']

            //渲染分页
            this.pageData.currentList = data.page.limit * 1
            this.pageData.currentPage = data.page.offset * 1
            this.pageData.currentTotal = data.page.total * 1
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！')
                return
            }
            this[obj] = true
        },

        //指派任务
        handleClickAssign () {
            this.checkedStatus('isShowAssign')
        },

        //确认指派
        handleConfirmAssign (val) {
            console.log(val)
        },

        // 优先级
        handleClickPriority () {
            this.checkedStatus('isShowPriority')
        },

        // 确认优先级
        handleConfirmPriority (val) {
            console.log(val)
        },

        // 领取任务
        handleClickTask () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！')
                return
            }
            this.$confirm('确认领取？', '提示', {
                type: 'warning'
            }).then(() => {

            })
        },

        //小组分类
        searchGroup (value) {
            console.log(value)
        },

    },
    created () {
        this.thisVm = this
        try {
            if (JSON.stringify(this.auditRecordInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1})
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['audit_record'])
                this.handleQueryInfoCallBack(this.auditRecordInfo)
            }
        } catch (e) {
            console.log(e)
        }
    },
}
</script>

<style lang="less" scoped>

@upload-height: 185px;
@border-color: #aaa;

.ui-table--supplier_code {
    font-size: 14px;
    font-weight: bold;
    line-height: 14px;

    p {
        margin-bottom: 10px;
    }
}

/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: @upload-height;
        }

        .ui-com_edit__upload--last_btn {
            border: 1px dashed @border-color;
            width: 100%;
            height: @upload-height;

            /deep/ .el-upload {
                display: block;
                height: @upload-height;
                line-height: @upload-height;

                span {
                    color: @border-color;
                }
            }
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}
</style>



