<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <el-input size="small" placeholder="请输入工号" v-model="queryData.supplierCode"></el-input>
                    </li>
                    <li>
                        <el-input size="small" placeholder="带出部门" v-model="queryData.supplierCode"></el-input>
                    </li>
                    <li>
                        <el-input size="small" placeholder="带出姓名" v-model="queryData.supplierCode">
                            <el-button slot="append">退出</el-button>
                        </el-input>
                    </li>
                </ul>
                <ul class="ui-scan-search">
                    <li>
                        <el-input size="small" placeholder="商品台车/精品SPU/试卖SPU" v-model="queryData.supplierCode">
                            <el-button slot="append">扫码</el-button>
                        </el-input>
                    </li>
                </ul>
            </div>

        </transition>
        <div class="ui-fn-module">
            <el-button type="text" size="small">
                <i class="icon-shanchusaoma icon_pink"></i>删除扫码
            </el-button>
            <el-button type="text" size="small">
                <i class="icon-querendaoyang icon_blue"></i>提交送样
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in optionList.moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.moduleType === item.value && 'ui-active'">{{item.label}}</span>
                    </li>
                </el-badge>
            </div>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import config from '@/assets/js/config'
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage
} from '@/services/index'
import {
    getCityInfo
} from '@/api/login'
import {
    getSupplierCreateListInfo,
    changeSupplierState,
    getEditInfo,
    saveEditInfo
} from '@/api/copy_department/copy_edit_module/copy_edit_list'

export default {
    data () {
        return {
            thisVm: {},

            queryData: {
                supplierCode: '',
                supplierName: '',
                supplierType: '',
                settlementType: '',
                devUser: '',
                deliverAddress: [],
                paymentMode: '',
                liaisons: '',
                createUser: '',
                createTime: [],
                state: '',
                moduleType: '1'
            },

            optionList: {
                moduleTypeList: [
                    {
                        label: '商品台车',
                        value: '1'
                    },
                    {
                        label: '商品SPU',
                        value: '2'
                    }
                ]
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

        }
    },
    methods: {

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true)
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true)
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val
        },

    },
}
</script>

<style lang="less" scoped>
.ui-main-module-search {
    li {
        width: 140px;
        margin-right: 2px !important;

        &:last-child {
            width: 183px;
        }
    }
}

.ui-scan-search {
    li {
        width: 467px;
    }
}

</style>



