/**  --->>>>   |^ 文案编辑记录^|   <<<<---**/

import http from '@/http'
import CONFIG from '@/assets/js/config'

// 获取文案编辑记录信息
export function getCopyEditRecordInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    }

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?'
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`
        }
        window.open('/supplier/supplier/supplier_export' + str, '_self')
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            })
        }))
    } else {
        return http.getAjax('/supplier/supplier/supplier_search', data)
    }
}

// 获取编辑信息
export function getEditInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }

    return http.getAjax('/supplier/supplier/edit_supplier_list', data)
}

// 保存编辑信息
export function saveEditInfo (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }

    return http.postAjax('/supplier/supplier/edit_supplier', data)
}



