<template>
    <el-dialog :visible="show" title="操作日志" width="800px" top="10vh" @close="handleClose">
        <ComTableInfo :tableData="tableData" :tableKey="tableKey" :tableProps="tableProps" :tableWidth="tableWidth"></ComTableInfo>
    </el-dialog>
</template>

<script>
    import {
        getOperationLogInfo
    } from '@/api/dev_department/supplier_development/supplier_create_list';
    export default {
        name: "OperationLog",
        props: ['id', 'show'],
        data() {
            return {
                tableData: [],
                tableKey: [],
                tableProps: [],
                tableWidth: ['140px', '80px']
            }
        },
        methods: {
            handleClose() {
                this.$emit('update:show')
            }
        },
        watch: {
            show(val) {
                console.log(val);
                if (val) {
                    getOperationLogInfo({
                        supplier_id: this.id
                    }).then(({ data }) => {
                        this.tableKey = data.data.key;
                        this.tableData = data.data.value.data;
                        this.tableProps = ['create_time', 'create_user', 'modify_reason', 'remark'];
                    })
                }
            }
        }
    }
</script>

<style scoped>

</style>


