import http from '@/http'
import CONFIG from '@/assets/js/config'

// TQE审核记录
export function getAuditRecordInfo (queryData = {}) {
    const data = {
        limit: 20,
        offset: 1,
        search_type: 1,
        ...CONFIG.ajaxData,
        ...queryData
    }

    // 查询参数带isExcel为导出
    if (data.isExcel) {
        let str = '?'
        for (let key in data) {
            str = `${str}&${key}=${data[key] || ''}`
        }
        window.open('/supplier/supplier/supplier_export' + str, '_self')
        return new Promise((resolve => {
            resolve({
                data: {
                    status: 2
                }
            })
        }))
    } else {
        return http.getAjax('/supplier/supplier/supplier_search', data)
    }
}



