/**  =========================> *|`| % 文案编辑记录 % |`|*<======================== **/

import * as types from '../../../mutation-type'

import {
    getCopyEditRecordInfo,
} from '../../../../api/copy_department/copy_edit_module/copy_edit_record'

const config = {
    state: {
        copyEditRecordInfo: {},
    },
    mutations: {
        [types.SAVE_COPY_EDIT_RECORD_INFO] (state, saveInfo) {
            state.copyEditRecordInfo = saveInfo
        }
    },
    actions: {
        // 获取创建供应商信息
        GetCopyEditRecordInfo ({commit}, queryData) {
            if (!queryData) {
                queryData = {}
            }
            return new Promise((resolve, reject) => {
                getCopyEditRecordInfo(queryData).then(response => {
                    if (response.data.status === 1) {
                        commit(types['SAVE_COPY_EDIT_RECORD_INFO'], response.data)
                    }
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })
            })
        }
    }
}

export default config




