<template>
    <el-dialog title="详情" :visible="show" width="1000px" @close="handleClose" class="ui-layout_edit-dialog">
        <div class="ui-query" v-if="detail">
            <div class="ui-title">
                <span>天眼数据</span>
            </div>
            <table>
                <tr>
                    <td>公司名称</td>
                    <td>{{detail.name}}</td>
                    <td>别称</td>
                    <td>{{detail.alias}}</td>
                    <td>英文名</td>
                    <td>{{detail.property3}}</td>
                </tr>
                <tr>
                    <td>法人代表</td>
                    <td>{{detail.legal_person_name}}</td>
                    <td>法人类型</td>
                    <td>{{detail.type}}</td>
                    <td>公司类型</td>
                    <td>{{detail.company_org_type}}</td>
                </tr>
                <tr>
                    <td>注册资金</td>
                    <td>{{detail.reg_capital}}</td>
                    <td>注册地址</td>
                    <td>{{detail.reg_location}}</td>
                    <td>省份简称</td>
                    <td>{{detail.base}}</td>
                </tr>
                <tr>
                    <td>核准时间</td>
                    <td>{{detail.approved_time}}</td>
                    <td>核准机构</td>
                    <td>{{detail.org_approved_institute}}</td>
                    <td>登记机关</td>
                    <td>{{detail.reg_institute}}</td>
                </tr>
                <tr>
                    <td>组织机构代码</td>
                    <td>{{detail.org_number}}</td>
                    <td>纳税人识别码</td>
                    <td>{{detail.tax_number}}</td>
                    <td>社会统一信用代码</td>
                    <td>{{detail.credit_code}}</td>
                </tr>
                <tr>
                    <td>经营状态</td>
                    <td>{{detail.reg_status}}</td>
                    <td>成立日期</td>
                    <td>{{detail.estiblish_time}}</td>
                    <td>经营结束时间</td>
                    <td>{{detail.to_time}}</td>
                </tr>
                <tr>
                    <td>行业</td>
                    <td>{{detail.industry}}</td>
                    <td>行业分数</td>
                    <td>{{detail.category_score}}</td>
                    <td>企业评分</td>
                    <td>{{detail.percentile_score}}</td>
                </tr>
                <tr>
                    <td>数据来源</td>
                    <td>{{detail.source_flag}}</td>
                    <td>数据刷新时间</td>
                    <td>{{detail.refresh_time}}</td>
                    <td>人数范围</td>
                    <td>{{detail.staff_num_range}}</td>
                </tr>
                <tr>
                    <td>经营范围</td>
                    <td colspan="5">{{detail.business_scope}}</td>
                </tr>
            </table>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        name: "QueryPreview",
        props: ['show', 'detail'],
        methods: {
            handleClose() {
                this.$emit('update:show', false);

                // `id` int(11) NOT NULL AUTO_INCREMENT,
                //     `eye_check_update_time` datetime DEFAULT NULL COMMENT '天眼数据更新时间 年月日',
                //     `from_time` datetime DEFAULT NULL COMMENT '公司经营开始时间',
                //     `type` tinyint(1) NOT NULL DEFAULT '2' COMMENT '法人类型（1 个人 2 公司）',
                //     `category_score` int(11) NOT NULL DEFAULT '0' COMMENT '行业分数',
                //     `company_id` int(11) NOT NULL DEFAULT '0' COMMENT '天眼查公司id',
                //     `reg_number` varchar(150) NOT NULL DEFAULT '' COMMENT '注册号',
                //     `percentile_score` int(11) NOT NULL DEFAULT '0' COMMENT '企业评分',
                //     `reg_capital` varchar(30) NOT NULL DEFAULT '' COMMENT '注册资金',
                //     `reg_institute` varchar(50) NOT NULL DEFAULT '' COMMENT '登记机关',
                //     `name` varchar(50) NOT NULL DEFAULT '' COMMENT '公司名',
                //     `reg_location` varchar(100) NOT NULL DEFAULT '' COMMENT '注册地址',
                //     `industry` varchar(50) NOT NULL DEFAULT '' COMMENT '所属行业',
                //     `approved_time` datetime DEFAULT NULL COMMENT '核准时间',
                //     `logo` varchar(255) NOT NULL DEFAULT '' COMMENT '公司logo',
                //     `org_approved_institute` varchar(30) NOT NULL DEFAULT '' COMMENT '核准机构',
                //     `tax_number` varchar(30) NOT NULL DEFAULT '' COMMENT '纳税人识别号',
                //     `business_scope` text COMMENT '经营范围',
                //     `property3` varchar(30) NOT NULL DEFAULT '' COMMENT '英文名',
                //     `org_number` varchar(30) NOT NULL DEFAULT '' COMMENT '组织机构代码',
                //     `reg_status` varchar(20) NOT NULL DEFAULT '' COMMENT '经营状态',
                //     `estiblish_time` datetime DEFAULT NULL COMMENT '成立日期',
                //     `legal_person_name` varchar(20) NOT NULL DEFAULT '' COMMENT '法人',
                //     `to_time` varchar(50) DEFAULT NULL,
                //     `legal_person_id` int(11) NOT NULL DEFAULT '0' COMMENT '法人id',
                //     `source_flag` varchar(30) NOT NULL DEFAULT '' COMMENT '来源',
                //     `actual_capital` varchar(30) NOT NULL DEFAULT '',
                //     `flag` tinyint(1) NOT NULL DEFAULT '0' COMMENT '0-显示 1-不显示（弃用）',
                //     `correct_company_id` varchar(20) NOT NULL DEFAULT '' COMMENT '新公司名id',
                //     `company_org_type` varchar(30) NOT NULL DEFAULT '' COMMENT '公司类型',
                //     `base` varchar(20) NOT NULL DEFAULT '' COMMENT '省份简称',
                //     `update_times` datetime DEFAULT NULL COMMENT '更新时间',
                //     `company_type` varchar(50) NOT NULL DEFAULT '',
                //     `credit_code` varchar(30) NOT NULL DEFAULT '' COMMENT '社会统一信用代码',
                //     `eye_company_id` int(11) NOT NULL DEFAULT '0' COMMENT '天眼表对应id',
                //     `refresh_time` datetime DEFAULT NULL COMMENT '数据刷新时间',
                //     `staff_num_range` varchar(30) NOT NULL DEFAULT '' COMMENT '人数范围',
                //     `is_micro_ent` varchar(50) NOT NULL DEFAULT '',
                //     `tags` varchar(255) NOT NULL DEFAULT '',
                //     `alias` varchar(20) NOT NULL DEFAULT '' COMMENT '别称',
                //     `social_staff_num` int(11) NOT NULL DEFAULT '0',
                //     `is_refresh_forward` tinyint(1) NOT NULL DEFAULT '0' COMMENT '1 是 ',
                //     `create_user` varchar(30) NOT NULL DEFAULT '' COMMENT '创建人',
                //     `create_time` datetime DEFAULT NULL COMMENT '创建时间',
                //     `modify_user` varchar(30) NOT NULL DEFAULT '' COMMENT '修改人',
                //     `modify_time` datetime DEFAULT NULL COMMENT '修改时间',
            }
        }
    };
</script>

<style scoped lang="less">
    @border-color: #e6e6e6;
    .ui-query {
        .ui-title {
            text-align: left;
            margin-bottom: 8px;
            span {
                color: #333333;
                font-size: 12px;
                padding-left: 5px;
                border-left: 2px solid #333333;
            }
        }

        table {
            width: 100%;
            border-collapse:collapse;
            td {
                border: 1px solid @border-color;
                text-align: left;
                padding: 10px 0 10px 10px;
                font-size: 12px;
                &:nth-child(odd) {
                    background: #f8f8f8;
                    width: 130px;
                    font-weight: bold;
                }

                &:nth-child(even) {
                    width: 170px;
                }
            }
        }
    }
</style>


