<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model.trim="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model.trim="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商类型">
                            <el-select v-model="queryData.supplierType" placeholder="请选择" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.supplierTypeList"
                                        :key="value"
                                        :label="label"
                                        :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="结算方式">
                            <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.settlementTypeList"
                                        :key="value"
                                        :label="label"
                                        :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="开发人" template="select" :options="optionList.devUserList"
                                           v-model="queryData.devUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="发货地址">
                            <el-cascader
                                    change-on-select
                                    v-model="queryData.deliverAddress"
                                    size="small"
                                    placeholder="请选择"
                                    :options="optionList.deliverAddressList"
                                    filterable
                            ></el-cascader>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="支付方式">
                            <el-select v-model="queryData.paymentMode" placeholder="请选择" size="small">
                                <el-option
                                        v-for="(label, value) in optionList.paymentModeList"
                                        :key="value"
                                        :label="label"
                                        :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="联络人">
                            <el-input size="small" v-model="queryData.liaisons"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="创建人" template="select" :options="optionList.createUserList"
                                           v-model="queryData.createUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="创建日期" template="daterange" v-model="queryData.createTime"></SearchRequirement>
                    </li>
                    <transition name="slide-fade" @after-enter="animation()" @after-leave="animation()" @before-enter="animation(1)">
                        <div v-show="filterShow">
                            <li>
                                <SearchRequirement label="状态">
                                    <el-select v-model="queryData.status" placeholder="请选择" size="small">
                                        <el-option
                                                v-for="(label, value) in optionList.statusList"
                                                :key="value"
                                                :label="label"
                                                :value="value"></el-option>
                                    </el-select>
                                </SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun">查询</el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="!filterShow">高级筛选<i class="el-icon-caret-bottom"></i></el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="filterShow">基本筛选<i class="el-icon-caret-top"></i></el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
        </div>
        <ComTableInfo
                isCheck
                ref="myTable"
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="tableWidth"
                :showSearch="showSearch"
                :otherHeight="['ui-shortcut_search']"
                @checked="handleSaveCheckInfo">
            <div class="ui-shortcut_search clearFix">
                <div class="ui-shortcut_search--item" v-for="list in optionList.shortcutSearchList">
                    <ul>
                        <li v-for="(item, index) in list">
                            <span @click="handleClickStatus(item.value)"><i :class="item.icon"></i>{{item.label}}({{item.number}})</span>
                            <i v-if="!index" class="icon-shenheliucheng"></i>
                        </li>
                    </ul>
                </div>
            </div>
            <template slot="supplier_code" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.supplier_code}}</p>
                    <div class="ui-table--supplier_code--flag">
                        <span>{{scope.row.type.replace('商', '')}}</span>
                    </div>
                    <!--<div class="ui-table&#45;&#45;icon_btn">-->
                    <!--<i :class="`icon-bianji${scope.row.status_code === '1' || scope.row.status_code === '4' ? '' : ' ui-disable'}`" @click.stop="handleClickEdit(scope.row)"></i>-->
                    <!--</div>-->
                </div>
            </template>
            <template slot="supplier_name" slot-scope="scope">
                <div>
                    <p>{{scope.row.supplier_name}}</p>
                    <div class="ui-table--icon_btn">
                        <el-popover
                                placement="bottom"
                                title="供应商链接"
                                width="200"
                                trigger="click">
                            <span>{{scope.row.web_link}}</span>
                            <el-button slot="reference" type="text"><i class="icon-gongyingshanglianjie"></i></el-button>
                        </el-popover>
                        <i class="icon-tianyancha" @click="handlePreviewDetail(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="shipping" slot-scope="scope">
                <div>
                    <p>{{`${scope.row.shipping_province}${scope.row.shipping_city}`}}</p>
                    <p>{{`${scope.row.shipping_area}${scope.row.shipping_address}`}}</p>
                </div>
            </template>
            <template slot="contacts_info" slot-scope="scope">
                <div>
                    <p><i class="icon-ren icon-plain"></i>{{`${scope.row.contacts}/${scope.row.phone}`}}</p>
                    <p><i class="icon-QQ icon-main"></i><a target="_self" :href="`tencent://message/?uin=${scope.row.qq}`">{{scope.row.qq}}</a></p>
                    <p><i class="icon-weixin icon-wx"></i>{{scope.row.wechat}}</p>
                    <p><i class="icon-youxiang icon-danger"></i>{{scope.row.email}}</p>
                </div>
            </template>
            <template slot="payment_info" slot-scope="scope">
                <div v-if="scope.row.pay_method === '线上支付宝'">
                    <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-zhifubaozhanghao1 icon-main"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-zhifubaomingcheng icon-main"></i>{{scope.row.receive_name}}</p>
                    <p><i class="icon-dianpumingcheng1 icon-dp"></i>{{scope.row.shop_name}}</p>
                </div>
                <div v-else-if="scope.row.pay_method === '线下境外'">
                    <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-ren icon-main"></i>{{`${scope.row.receive_name}/${scope.row.receive_mobile}`}}</p>
                    <p><i class="icon-yinhangqiazhanghao icon-main"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-shenfenzhenghaoma icon-dp"></i>{{scope.row.receive_id_number}}</p>
                </div>
                <div v-else-if="scope.row.pay_method === '线下境内'">
                    <p><i class="icon-zhifufangshi icon-warn"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-gongsimingcheng icon-main"></i>{{scope.row.receive_name}}</p>
                    <p><i class="icon-yinhangqiazhanghao icon-main"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-kaihuhang icon-dp"></i>{{scope.row.receive_bank}}</p>
                </div>
            </template>
            <template slot="operate_dev_user" slot-scope="scope">
                <div>
                    <p v-if="scope.row.devlop_user"><span>开发人：</span><span>{{scope.row.devlop_user}}</span></p>
                    <p v-if="scope.row.create_user"><span>创建人：</span><span>{{scope.row.create_user}}</span></p>
                    <p v-if="scope.row.check_user"><span>审核人：</span><span>{{scope.row.check_user}}</span></p>
                </div>
            </template>
            <template slot="file_url" slot-scope="scope">
                <ul class="ui-table_file">
                    <li v-for="(name, key) in fileTypeList" v-if="scope.row[key]">
                        <div class="ui-table_file--item">
                            <img :src="scope.row[key]" alt="" @click="handleLookImage(scope.row[key])">
                        </div>
                        <span>{{ name }}</span>
                    </li>
                </ul>
            </template>
	    <template slot="status" slot-scope="scope">
                <div v-if="scope.row.status_code == '2'|| scope.row.status_code == '5'"><span class="ui-state_circle state_orange"></span>{{scope.row.status}}</div>
                <div v-if="scope.row.status_code == '1'|| scope.row.status_code == '3'"><span class="ui-state_circle state_blue"></span>{{scope.row.status}}</div>
                <div v-if="scope.row.status_code == '4'|| scope.row.status_code == '6'"><span class="ui-state_circle state_red"></span>{{scope.row.status}}</div>
            </template>
            <template slot="operation" slot-scope="scope">
                <el-button type="text" @click="handleOperaiontLog(scope.row)">操作日志</el-button>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <QueryPreview :show.sync="isQueryPreview" :detail="queryPreviewData"></QueryPreview>
        <OperationLog :show.sync="isOpeLogShow" :id="saveCheckId"></OperationLog>
    </div>
</template>

<script>
    import QueryPreview from './SupplierCreateList/QueryPreview'
    import OperationLog from './SupplierCreateList/OperationLog'
    import config from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage
    } from '@/services/index'
    import {
        mapActions,
        mapGetters
    } from "vuex";
    import {
        getSupplierCreateListInfo
    } from '@/api/dev_department/supplier_development/supplier_create_list';
    import {
        getSupplierDetailInfo
    } from '@/api/login';

    export default {
        components: {
            QueryPreview,
            OperationLog
        },
        data() {
            return {

                //高级筛选
                filterShow: false,

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    supplierCode: '',
                    supplierName: '',
                    supplierType: '',
                    settlementType: '',
                    devUser: '',
                    deliverAddress: [],
                    paymentMode: '',
                    liaisons: '',
                    createUser: '',
                    createTime: '',
                    status: ''
                },

                optionList: {
                    supplierTypeList: {},
                    settlementTypeList: {},
                    devUserList: {},
                    deliverAddressList: [],
                    paymentModeList: {},
                    liaisonsList: {},
                    createUserList: {},
                    shortcutSearchList: [
                        [
                            {
                                label: '开发部',
                                value: '',
                                number: '0',
                                icon: 'icon-kaifabu icon_blue',
                            },
                            {
                                label: '已创建',
                                value: '1',
                                number: '0'
                            },
                            {
                                label: '被驳回',
                                value: '4',
                                number: '0'
                            }
                        ],
                        [
                            {
                                label: '供应链',
                                value: '3',
                                number: '0',
                                icon: 'icon-gongyinglian icon_orange'
                            },
                            {
                                label: '待审核',
                                value: '2',
                                number: '0'
                            },
                            {
                                label: '已审核',
                                value: '3',
                                number: '0'
                            }
                        ],
                        [
                            {
                                label: '开发部',
                                value: '3',
                                number: '0',
                                icon: 'icon-kaifabu icon_blue'
                            },
                            {
                                label: '待禁用',
                                value: '5',
                                number: '0'
                            },
                            {
                                label: '已禁用',
                                value: '6',
                                number: '0'
                            }
                        ]
                    ]
                },

                tableData: [],
                tableWidth: [],
                tableKey: [],
                tableProps: [],

                // 保存勾选数据
                saveCheckInfo: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 预览图片
                isShowLookImage: false,
                lookImageUrl: [],
                fileTypeList: {
                    bl_url: '营业执照',
                    tca_url: '收款协议',
                    gtc_url: '纳税人证明',
                    b_r_rul: '开票记录',
                },

                // 状态
                statusList: {
                    '1': '已创建',
                    '2': '待审核',
                    '3': '已审核',
                    '4': '被驳回',
                    '5': '待禁用',
                    '6': '已禁用'
                },

                // 天眼查预览
                isQueryPreview: false,
                queryPreviewData: {},

                // 操作日志
                saveCheckId: '',
                isOpeLogShow: false,
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'supplierCreateLogInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetSupplierCreateLogInfo'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,

            // 列表信息查询
            handleQueryInfo(params = {}, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['supplier_create_log']);
                }

                let obj = {
                    status: this.queryData.status,
                    supplier_code: this.queryData.supplierCode.replace(/\s+/g, ','),
                    type: this.queryData.supplierType,
                    shipping_province: this.queryData.deliverAddress[0],
                    shipping_city: this.queryData.deliverAddress[1],
                    settle_method: this.queryData.settlementType,
                    create_user: this.queryData.createUser,
                    devlop_user: this.queryData.devUser,
                    supplier_name: this.queryData.supplierName,
                    contacts: this.queryData.liaisons,
                    pay_method: this.queryData.paymentMode,
                    start_time: this.queryData.createTime[0],
                    end_time: this.queryData.createTime[1],
                    ...params
                };

                this.GetSupplierCreateLogInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 查询重置
            handleResetQueryInfo() {
                this.queryData = {
                    supplierCode: '',
                    supplierName: '',
                    supplierType: '',
                    settlementType: '',
                    devUser: '',
                    deliverAddress: [],
                    paymentMode: '',
                    liaisons: '',
                    createUser: '',
                    createTime: '',
                    status: '',
                }
            },

            //高级筛选
            handleClickFilter() {
                this.filterShow = !this.filterShow;
            },

            animation(isHide) {
                if (isHide) {
                    console.log(document.querySelector('body'));
                    document.querySelector('body').style.overflow = 'hidden';
                    return;
                }
                this.$refs.myTable.handleRefreshTableHeight()
            },


            // 分页条数切换
            handleSizeChange(val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true)
            },

            // 跳转页数切换
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            // 切换状态查询
            handleClickStatus(val) {
                this.queryData.status = val;
                this.handleQueryInfo({ limit: this.pageData.currentList })
            },

            // 保存勾选数据
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 操作日志
            handleOperaiontLog({ supplier_id }) {
                this.saveCheckId = supplier_id;
                this.isOpeLogShow = true;
            },

            // 导出明细信息
            handleExportListInfo(type) {
                return;
                if (type === '_all') {
                    this.handleQueryInfo({
                        isExcel: 1
                    }, true);
                } else {
                    this.handleQueryInfo({
                        isExcel: 1
                    }, true);
                }
            },

            // 天眼查数据预览
            handlePreviewDetail({ credit_code }) {
                getSupplierDetailInfo({
                    credit_code: credit_code
                }).then(({ data }) => {
                    this.queryPreviewData = data.result;
                    this.isQueryPreview = true;
                });
            },

            // 预览图片
            handleLookImage(src) {
                this.isShowLookImage = true;
                this.lookImageUrl = src;
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo({ limit: this.pageData.currentList });
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            },

            //查询回调
            handleQueryInfoCallBack(data){

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'supplier_create_log',
                    value: JSON.stringify(this.queryData)
                });

                // 渲染下拉
                this.optionList.supplierTypeList = data.select_list.sup_type;
                this.optionList.settlementTypeList = data.select_list.sup_settle_method;
                this.optionList.devUserList = data.select_list.devlop_user;
                this.optionList.paymentModeList = data.select_list.sup_pay_method;
                // this.optionList.liaisonsList = data.select_list.sup_pay_method;
                this.optionList.createUserList = data.select_list.create_user;
                this.optionList.stateList = data.select_list.sup_status;

                //数据渲染
                this.tableData = data.data.value;
                this.tableKey = [...data.data.key];
                this.tableKey.push('操作');
                this.tableWidth = ['80px', '100px', '180px', '150px', '180px', '180px', '180px', '240px', '80px', '', '100px'];

                // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
                this.tableProps = ['index', 'supplier_code', 'supplier_name', 'shipping', 'contacts_info', 'payment_info', 'operate_dev_user', 'file_url', 'status', 'reject_remarks'];
                // this.tableWidth = ['60', '150', '250', '150', '150', '180', '150', 'file_url', '80', 'reject_remarks'];

                //渲染分页
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;

                // 渲染流程
                let listArr = [
                    data.select_list.department_count.develop,
                    data.select_list.department_count.supply_chain,
                    data.select_list.department_count.disable
                ];

                this.optionList.shortcutSearchList.forEach((value, index) => {
                    this.optionList.shortcutSearchList[index][0].number = listArr[index];
                    for (let i = 1; i < value.length; i++) {
                        value[i].number = data.select_list.status_count[value[i].value];
                    }
                })
            }
        },
        created() {
            this.thisVm = this;
            try{
                if (JSON.stringify(this.supplierCreateLogInfo) === '{}') {
                    this.handleQueryInfo({is_get_base: 1});
                }else{
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['supplier_create_log']);
                    this.handleQueryInfoCallBack(this.supplierCreateLogInfo);
                }
            }catch (e) {
                console.log(e)
            }
        },
    };
</script>

<style lang="less" scoped>

    @upload-height: 185px;
    @border-color: #aaa;

    .ui-table_file {
        li {
            float: left;
            margin-right: 10px;
            border: 1px solid #ccc;
            text-align: center;
            background: #FFFFFF;
            padding-bottom: 5px;
            .ui-table_file--item {
                width: 70px;
                height: 80px;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            span {
                font-size: 12px;
                color: #666666;
            }
        }
    }

    .ui-table--supplier_code {
        font-size: 12px;
        font-weight: bold;
        line-height: 10px;

        p {
            margin-bottom: 8px;
        }
    }

    /deep/ .el-table td, /deep/ .el-table th {
        overflow: hidden;
        text-align: left!important;
    }

    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        background: #63c4e9;
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
        font-weight: normal;
    }

    .ui-table--icon_btn {
        i {
            cursor: pointer;
        }
    }

    .ui-disable {
        color: #999999!important;
    }

    .ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
        li {
            width: 130px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            img {
                width: 100%;
                height: @upload-height;
            }

            .ui-com_edit__upload--last_btn {
                border: 1px dashed @border-color;
                width: 100%;
                height: @upload-height;

                /deep/ .el-upload {
                    display: block;
                    height: @upload-height;
                    line-height: @upload-height;

                    span {
                        color: @border-color;
                    }
                }
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: @upload-height;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: 185px;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }

    .ui-shortcut_search {
        height: 70px;
        background: #FFFFFF;
        padding-left: 30px;
        /*padding-bottom: 10px;*/

        .ui-shortcut_search--item {
            float: left;
            margin-right: 120px;

            li {
                border-bottom: 1px dashed #666666;
                border-left: 1px dashed #666666;
                position: relative;

                span {
                    margin-left: 20px;
                    width: 100%;
                    display: block;
                    background: #FFFFFF;
                    position: relative;
                    top: 10px;
                    /*padding-left: 6px;*/
                    color: #666666;
                    cursor: pointer;
                    z-index: 1;
                    font-size: 12px;

                }

                &:first-child {
                    font-weight: bold;
                    border: 0 none;
                    span {
                        margin: 0;
                        /*padding-left: 14px;*/
                        color: #333333;
                        padding: 2px 0;
                    }

                    .icon-shenheliucheng {
                        position: absolute;
                        z-index: 1;
                        top: 10px;
                        background: #FFFFFF;
                        left: -8px;
                    }
                }
                i {
                    margin-left: -7px;
                    margin-right: 5px;
                }
            }
        }
    }
</style>


